@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-image: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url(/12MiaOne/static/media/bg.3d7e59a8.jpg);
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header{
  font-size: 80px;
  font-weight: bold;
  font-family: 'Fredoka One', cursive;
  letter-spacing: 7px;
  text-align: center;
  color: rgb(255,255,255,0.7);

  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  transition: all ease-in-out 250ms;
}

.num{
  font-size: 300px;
  color: #4DC247;
  opacity: 0.5;
  position: relative;
  top: 60px;
  transition: all ease-in-out 250ms;
}

.tagline{
  font-size: 45px;
}
.header:hover{
  font-size: 85px;
  color:rgb(255,255,255);
}

.tagline:hover{
  font-size: 47px;
}

.num:hover{
  font-size: 302px;
  opacity: 1;
}

.btns{
  position: absolute;
  top: 71vh;
  left: 50%;
  transform: translateX(-50%);

  width: 300px;
  height: 50px;

  display: flex;
  justify-content: space-around;
}

.anggotabtn, .Photos{
  outline: none;
  border: none;
  border-radius: 20px;
  width: 85px;
  height: 45px;

  background: rgb(255,255,255,0.4);
  font-size: 15px;
  font-family: 'Rajdhani', sans-serif;
  font-weight: bold;
  transition: all ease-in-out 150ms;
  cursor: pointer;
}

.anggotabtn:hover, .Photos:hover{
  width: 87px;
  height: 47px;
}

.anggotabtn:hover{
  background: rgb(255,255,224);
}

.Photos:hover{
  background: rgb(135,206,250);
}
.Photos:active, .anggotabtn:active{
  width: 85px;
  height: 45px;
}


.Container{
  position: absolute;
  top: 100vh;
  width: 100%;
  height: 420px;

  background: #14171A;
}

.wali{
  position: relative;
  top: 20px;
  width: 50%;
  height: 400px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.namaWali{
  font-size: 30px;
  font-family: 'Comfortaa', cursive;
  color: white;
}

.descWali{
  font-size: 25px;
  font-family: 'Comfortaa', cursive;
  color: white;
}

.picwali{
  margin-bottom: 10px;
  width: 200px;
  border-radius: 50px;
  border: 2px solid darkgrey;
}

